workbox_routing_registerRoute(/^https:\/\/fonts\.(?:googleapis|gstatic)\.com\/.*/i, new workbox_strategies_CacheFirst({ "cacheName":"google-fonts", plugins: [new workbox_expiration_ExpirationPlugin({ maxEntries: 30, maxAgeSeconds: 31536000 }), { handlerDidError: async ({ request }) => self.fallback(request) }] }), 'GET');
workbox_routing_registerRoute(/^https?.*/, new workbox_strategies_NetworkFirst({ "cacheName":"https-cache","networkTimeoutSeconds":15, plugins: [new workbox_expiration_ExpirationPlugin({ maxEntries: 150, maxAgeSeconds: 604800 }), new workbox_cacheable_response_CacheableResponsePlugin({ statuses: [ 0, 200 ] }), { handlerDidError: async ({ request }) => self.fallback(request) }] }), 'GET');




